.intro {
   &__info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 15px;
   }
   .title {
      text-align: left;
   }
   &__text {
      flex: 1 1 auto;
      display: flex;
      justify-content: flex-start;
      align-items: flex-end;
      position: relative;
      overflow: hidden;
      z-index: 3;
      color: var(--white-p);
      @media (max-width: 1300px) {
         font-size: 16px;
         padding: 0px;
      }
   }
   &__img {
      height: 100%;
      width: 100%;
      position: relative;
      aspect-ratio: 1 / 1;
   }
   &__img img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
   }
}
@media (max-width: $tablet) {
   .intro {
      &__body {
         flex-direction: column-reverse;
      }
      &__info {
         gap: 20px;
         padding: 5px;
      }
      &__text {
         font-size: 18px;
      }
   }
}
