.header {
   width: 100%;
   position: fixed;
   top: 0;
   left: 0;
   user-select: none;
   z-index: 200;
   background: var(--black-p);
   border-bottom: 1px solid var(--border);
   transition: 0.3s;
   &__body {
      display: flex;
      flex-direction: column;
      gap: 30px;
      position: relative;
      padding: 15px 30px;
   }
   &__logo {
      z-index: 5;
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 15px;
      padding-top: 4px;
   }
   &__svg {
      color: var(--white-p);
   }
   &__top {
      display: flex;
      justify-content: space-between;
   }
   &__bottom-wrapper {
      display: none;
   }
}
.menu {
   display: flex;
   align-items: center;
   gap: 20px;
   &__list {
      display: flex;
      z-index: 5;
   }
   &__item a {
      color: var(--white-p);
      padding: 10px 15px;
   }
}
.settings {
   z-index: 5;
   &__mode-btn {
      position: relative;
      width: 36px;
      height: 36px;
      display: flex;
      align-items: center;
      justify-content: center;
   }
   &__icon {
      color: var(--white-p);
      position: absolute;
      transition: 0.2s;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 36px;
      height: 36px;
      div {
         width: 32px;
         height: 32px;
      }
      svg {
         width: 32px;
         height: 32px;
      }
   }
   // change icon on click
   &__icon:nth-child(1) {
      opacity: 1;
   }
   &__icon:nth-child(2) {
      opacity: 0;
   }
   .darkmode &__icon:nth-child(1) {
      opacity: 0;
   }
   .darkmode &__icon:nth-child(2) {
      opacity: 1;
   }
}

@media (max-width: $mobile) {
   .header {
      &__body {
         display: flex;
         flex-direction: column;
         justify-content: space-between;
         padding: 0px;
      }
      &__top {
         position: relative;
         z-index: 5;
         padding: 15px 30px;
      }
      &__bottom-wrapper {
         position: fixed;
         bottom: 0;
         left: 0;
         width: 100%;
         height: 100svh;
         display: flex;
         flex-direction: column;
         justify-content: flex-end;
         pointer-events: none;
      }
      &__bottom {
         background: var(--black-p);
         pointer-events: all;
         width: 100%;
         border-top: 1px solid var(--border);
         padding: 15px 30px;
         display: flex;
         justify-content: space-around;
         text-align: center;
      }
      &__svg {
         color: var(--white-p);
      }
   }
   .menu {
      &__list {
         display: none;
      }
      &__item a {
         padding: 0px;
      }
   }
}
@media (max-width: $mobileSmall) {
   .header {
      &__bottom {
         justify-content: space-between;
      }
   }
}
