// title
.title {
   font-weight: 500;
   font-style: italic;
   font-size: 50px;
   line-height: 1;
   color: var(--white-p);
   font-family: 'Playfair Display', serif;

   @media (max-width: 1150px) {
      font-size: 40px;
   }
   @media (max-width: $mobileSmall) {
      font-size: 30px;
   }
}
//text
.text {
   font-style: italic;
   font-size: 20px;
   @media (max-width: 1300px) {
      font-size: 16px;
      padding: 0px;
   }
   @media (max-width: $tablet) {
      &__text {
         font-size: 18px;
      }
   }
}
//section
.section {
   width: 100%;
   padding: 0 50px;
   @media (max-width: $tablet) {
      padding: 0 15px;
   }
   &__body {
      width: 100%;
      border-bottom: 1px solid var(--border);
      padding: 50px;
      @media (max-width: $tablet) {
         padding: 30px;
      }
      @media (max-width: $mobileSmall) {
         padding: 15px;
      }
   }
   &:last-child .section__body {
      border-bottom: 0px solid var(--border);
   }
}

.right,
.left {
   display: grid;
   grid-template-rows: 1fr;
   width: 100%;
   gap: 50px;
   @media (max-width: $tablet) {
      display: flex;
      flex-direction: column;
      gap: 25px;
   }
}
//section image left side
.left {
   grid-template-columns: 1.25fr 0.75fr;
   .title {
      text-align: right;
      @media (max-width: $tablet) {
         text-align: left;
      }
   }
   @media (max-width: $tablet) {
      flex-direction: column-reverse;
   }
}
//section image tight side
.right {
   grid-template-columns: 0.75fr 1.25fr;
   .title {
      text-align: left;
   }
}

// list
.list {
   padding: 20px 30px;
   border: 1px solid var(--border);
   display: grid;
   grid-template-columns: 1fr 1fr;
   gap: 5px;
   > li {
      &::before {
         content: '- ';
      }
   }
}
