// import fonts/font <=>
@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@300;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Glass+Antiqua&family=Playfair+Display:ital,wght@0,400;0,500;1,400;1,500&display=swap');
// default font / default font size <=>
$fontFamily: 'Lexend', sans-serif;
$fontSize: 14;
// transition for dark mode and light
* {
   transition: color 0.2s;
   transition: background 0.2s;
}
// colors palette

:root {
   --black-p: #10100e;
   --black-s: #191815;
   --black-t: #19181580;
   --white-p: #fffff2;
   --white-s: #dadada;
   --border: #242424;
}

.darkmode {
   --black-p: #fffff9;
   --black-s: #fffffd;
   --black-t: #fffffd80;
   --white-p: #1a1a16;
   --white-s: #191815;
   --border: #aeaeae;
}
// max width for container <=>
$maxWidthContainer: 1250;
// size gadjes for adapted and other <=>
$tablet: 991.98px;
$mobile: 767.98px;
$mobileSmall: 479.98px;
// null style <=>
@import 'scss/_null.scss';
// general style <=>
html {
   scroll-behavior: smooth;
}
body {
   line-height: 1.4;
   font-family: $fontFamily;
   font-size: $fontSize + px;
   background: var(--black-p);
   animation: mainanim 0.7s ease-in-out;
   color: var(--white-s);
   font-weight: 300;
   position: relative;
}
@keyframes mainanim {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}
// wrapper <=>
#root,
.App,
body {
   min-height: 100%;
   display: flex;
   flex-direction: column;
   .main {
      padding-top: 70px;
      flex: 1 1 auto;
      @media (max-width: $mobile) {
         padding-bottom: 80px;
      }
   }
}
// container <=>
.container {
   max-width: $maxWidthContainer + px;
   margin: 0 auto;
   //fix for slider
   > * {
      min-width: 0;
   }
}

// scroll bar <=>
@media only screen and (max-width: $mobile) {
   ::-webkit-scrollbar {
      display: none;
   }
}
::-webkit-scrollbar {
   background-color: var(--black-p);
   width: 16px;
}
::-webkit-scrollbar-track {
   background-color: var(--black-p);
}
::-webkit-scrollbar-thumb {
   background-color: var(--white-p);
   border-radius: 16px;
   border: 4px solid var(--black-p);
}
::-webkit-scrollbar-thumb:hover {
   background-color: var(--white-s);
}
::-webkit-scrollbar-thumb:active {
   background-color: var(--white-p);
}
::-webkit-scrollbar-button {
   display: none;
}

// when copu text<=>
::selection {
   color: var(--black-p);
   background: var(--white-p);
}

// imports
@import './scss/_utils';
@import './scss/_header';
@import './scss/_intro';
@import './scss/_skills';
@import './scss/_footer';
